.productItemContainer {
    background-color: white;
    margin: 10px;
    padding: 18px;
    text-align: center;
    width: 250px;
    height: 350px;
}

.productTitleContainer {
  width: 100%;
  height: 50px;
  overflow: hidden;
  margin-top: 8px;
  margin-bottom: 8px;
}

.productDescriptionContainer {
  width: 100%;
  height: 100px;
  overflow: hidden;
}

.productImage {
    height: 150px;
    overflow: hidden;
}

.detailLink {
  color: #8980F5;
  text-decoration: none;
}
.ProductImage {
    width: 80%;
    height: 80%;
}

body {
    background-color: lightgrey;
}
.titleHeader {
    margin-top: 15px;
    margin-left: 15px;
}
form.search {
  overflow: hidden;
  margin-bottom: 10px;
}

.input-wrapper {
  width: 100%;
  display:inline-block;
  position: relative
}

.fa-search {
  position: absolute;
  top: 8px;
  right: 16px;
  cursor: pointer;
}

.search-btn {
  border:none; 
  width: 0px;
  height: 0px;
  padding: 0;
}
.navbar-dark {
  background-color: #8980F5 !important;
}
.btn-blue {
  color: white;
  background-color: #1B2CC1;
}

.shocked-emoji {
  font-size: 100px;
}
input[type=text] {
  width: 100%;
  margin-bottom: 20px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

label {
  margin-bottom: 10px;
  display: block;
}

.icon-container {
  margin-bottom: 20px;
  padding: 7px 0;
  font-size: 24px;
}

